*{
   color: limegreen;
   font-family:  Helvetica,Arial, sans-serif;
}

h2{
   color: limegreen;
   font-size: 2.5rem;
  fill-opacity: unset;
   font-family: fantasy;
}

body{
padding: 60px ;
/* background-color: lavender; */
background-image: url("https://lh3.googleusercontent.com/MYFPz2DTPNhFkrGclFrTBdwn6EbUivvNf52fF5Lp-HOhWsLROLwLUiRalsEi52NsH0nYjGvi5CKbyr6xiNUPErrQQg=w640-h400-e365-rj-sc0x00ffffff");

}

input{
   color: #db0606;
   font-size: 1.2rem;
   width: 100%;
}

.card-grid{
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
}

.card{

   align-content: center;
   border: 1px solid grey;
   border-radius: 6px;
   color: #db0606;
   margin-right: 10px;
   margin-bottom: 10px;
   overflow: hidden;
}

.card p {
   padding: 5px;
   text-align: center;
}

.card img {
   align-content: center;
   max-height: 170px;
}